body {
  font-family: "Roboto", sans-serif;
}

.btn-info.active {
  background-color: limegreen !important;
}

.btn-info.Default {
  background-color: white !important;
}

#components-page-header-demo-content .image {
  margin: 0 0 0 60px;
  display: flex;
  align-items: center;
}

#components-page-header-demo-content .example-link {
  margin-right: 16px;
}
#components-page-header-demo-content .example-link-icon {
  margin-right: 8px;
}

#components-page-header-demo-content .ant-page-header-rtl .example-link {
  float: right;
  margin-right: 0;
  margin-left: 16px;
}
#components-page-header-demo-content .ant-page-header-rtl .example-link-icon {
  margin-right: 0;
  margin-left: 8px;
}

@media (max-width: 768px) {
  #components-page-header-demo-content .image {
    flex: 100%;
    margin: 24px 0 0;
  }
}

.site-layout-content {
  background: #fff;
  padding: 24px;
  min-height: 280px;
}
#components-layout-demo-top .logo {
  width: 120px;
  height: 31px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px 24px 16px 0;
  float: left;
}

.site-card-wrapper {
  background: #ececec;
  padding: 30px;
}

.vs {
  background: #2879d4;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 40px;
  text-align: center;
  height: 40px;
  line-height: 2.4;
  border-radius: 50%;
  z-index: 1;
  color: white;
  font-size: 16px;
}

.chartFilterPane {
  position: relative;
  padding: 20px;
}

.paddingLeft25 {
  padding-left: 25px;
}

.leftPane {
  background: rgb(237, 237, 237);
  padding: 30px;
  border-top-left-radius: 8px !important;
  border-bottom-left-radius: 8px !important;
  color: wheat;
}

.rightPane {
  background-color: rgb(219, 222, 227);
  justify-content: center;
  border-top-right-radius: 8px !important;
  border-bottom-right-radius: 8px !important;
  align-items: center;
  display: flex;
  width: 100%;
}

.chartHeader {
  margin-top: 15px;
}

.chartContainer {
  margin-top: 15px;
  margin-right: 5px;
  width: 100%;
}

.leftPane h2 {
  color: "black";
}

.leftPane p {
  margin-top: -14px;
  color: black;
}

.chartHeader
  .ant-radio-group-solid
  .ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):active {
  background: rgb(55, 173, 169);
  border-color: rgb(55, 173, 169);
  border-radius: 5px;
}

.chartHeader
  .ant-radio-group-solid
  .ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled) {
  background: rgb(55, 173, 169);
  border-color: rgb(55, 173, 169);
  border-radius: 5px;
}

.ant-radio-button-wrapper {
  color: rgb(55, 173, 169);
  border: 0px !important;
  position: static !important;
  font-weight: 500;
}

.ant-radio-button-wrapper:first-child {
  border-left: 0px;
  border-radius: 2px 0 0 2px;
}

.highcharts-credits {
  display : none;
}
